import {
ONE,
TWO,
THREE,
FOUR,
FIVE,
SIX,
SEVEN,
EIGHT } from './mutation-types.js'
const state = {
  one: {
    isRight: false,
    answerData: ''
  },
  two: {
    isRight: false,
    answerData: ''
  },
  three: {
    isRight: false,
    answerData: ''
  },
  four: {
    isRight: false,
    answerData: ''
  },
  five: {
    isRight: false,
    answerData: ''
  },
  six: {
    isRight: false,
    answerData: ''
  },
  seven: {
    isRight: false,
    answerData: ''
  },
  eight: {
    isRight: false,
    answerData: ''
  }
}

const mutations = {
  [ONE] (state, payload) {
    state.one.isRight = payload.data
    state.one.answerData = payload.answer
  },
  [TWO] (state, payload) {
    state.two.isRight = payload.data
    state.two.answerData = payload.answer
  },
  [THREE] (state, payload) {
    state.three.isRight = payload.data
    state.three.answerData = payload.answer
  },
  [FOUR] (state, payload) {
    state.four.isRight = payload.data
    state.four.answerData = payload.answer
  },
  [FIVE] (state, payload) {
    state.five.isRight = payload.data
    state.five.answerData = payload.answer
  },
  [SIX] (state, payload) {
    state.six.isRight = payload.data
    state.six.answerData = payload.answer
  },
  [SEVEN] (state, payload) {
    state.seven.isRight = payload.data
    state.seven.answerData = payload.answer
  },
  [EIGHT] (state, payload) {
    state.eight.isRight = payload.data
    state.eight.answerData = payload.answer
  }
}

export default {
  state,
  mutations
}



