import Vue from 'vue'
import Router from 'vue-router'
import index from '../page/index.vue'
import one from '../page/one.vue'
import two from '../page/two.vue'
import three from '../page/three.vue'
import four from '../page/four.vue'
import five from '../page/five.vue'
import six from '../page/six.vue'
import seven from '../page/seven.vue'
import eight from '../page/eight.vue'
import end from '../page/end.vue'
import store from '../store'

Vue.use(Router)

const routes = [{
  path: '/',
  name: 'index',
  component: index
}, {
  path: '/1',
  name: 'one',
  component: one
}, {
  path: '/2',
  name: 'two',
  component: two
}, {
  path: '/3',
  name: 'three',
  component: three
}, {
  path: '/4',
  name: 'four',
  component: four
}, {
  path: '/5',
  name: 'five',
  component: five
}, {
  path: '/6',
  name: 'six',
  component: six
}, {
  path: '/7',
  name: 'seven',
  component: seven
}, {
  path: '/8',
  name: 'eight',
  component: eight
}, {
  path: '/end',
  name: 'end',
  component: end
}]

const router = new Router({
  routes
})

router.beforeEach((to, from, next) => {
  if (store.state.data.iscomeFirst) {
    store.commit('COMEIN')
    next('/')
  } else {
    next()
  }
})

export default router



