<template>
  <transition name='fade'> 
  <div class="one" :style="homeHight">
    <div class="content" :class='{Begin: isBegin, End: isEnd}'>
      <p class="title">1、{{ title }}</p>
      <p v-for='(item,index) in optionsData' @click='showItem(index)'>
        <!-- <img :src="item.imgdata" :class='{showItemCss: item.isshowItem}'> --> {{item.text}}
      </p>
      <p> 1 / 8 </p>
    </div>
    <img @load='showContent' class="logo" src="../../static/img/bit_logo.png">
  </div>
  </transition>
</template>
<script>
export default {
  data () {
    return {
      title: '',
      optionsData: [{
        text: ' '
      }, {
        text: ' '
      }, {
        text: ' '
      }, {
        text: ''
      }],
      answer: '',
      isBegin: false,
      isEnd: false,
      homeHight: {
        height: '',
        background: ''
      }
    }
  },
  methods: {
    showItem (index) {
      const path = '/2/'
      setTimeout(() => this.$router.push({path: path}), 1000)
      this.isEnd = true
      if (index === this.answer) {
        let answerData = this.$store.getters.flagData[0].answer
        // console.log(answerData)
        this.$store.commit('ONE', {
          data: true,
          answer: '1、' + answerData
        })
      }
    },
    showContent () {
      this.isBegin = true
    }
  },
  created () {
    this.homeHight.height = document.documentElement.clientHeight + 'px'
    let data = this.$store.getters.flagData[0]
    for (let i = 0; i < 4; i++) {
      if (data.option[i].answerRight) {
      }
    }
    // console.log(data.option)
    this.homeHight.backgroundImage = data.bgImg
    // console.warn(this.homeHight.background)
    this.title = data.title
    this.optionsData = data.option
    for (let i = 0; i < 4; i++) {
      if (data.option[i].answerRight) {
        this.answer = i
        // console.log(this.answer)
      }
    }
    let answerData = this.$store.getters.flagData[0].answer
    this.$store.commit('ONE', {
      data: false,
      answer: '1、' + answerData
    })
    let wx = require('weixin-js-sdk')
    let wxConfig = this.$store.state.data.wxapi
    // console.warn(wxConfig)
    wx.config({
      debug: false,  // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
      appId: wxConfig.appId,  // 必填，公众号的唯一标识
      timestamp: wxConfig.timestamp,   // 必填，生成签名的时间戳
      nonceStr: wxConfig.nonceStr,  // 必填，生成签名的随机串
      signature: wxConfig.signature,  // 必填，签名，见附录1
      jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone']   // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
    })
    wx.ready(function () {
      let dataOne = {
        title: '梦回童年，快来测测你还能不能过六一儿童节吧,♪(๑ᴖ◡ᴖ๑)♪儿童节快乐',
        link: 'https://test.stuzone.com',
        imgUrl: 'http://i4.buimg.com/524586/608bd17fa3087f9b.png'   // https://ooo.0o0.ooo/2017/05/27/592957592a99e.png
      }
      let dataTwo = {
        title: '梦回童年，快来测测你还能不能过六一儿童节吧！',
        desc: '♪(๑ᴖ◡ᴖ๑)♪儿童节快乐', // 分享描述
        link: 'https://test.stuzone.com',
        imgUrl: 'http://i4.buimg.com/524586/608bd17fa3087f9b.png'
      }
      wx.onMenuShareTimeline(dataOne)
      wx.onMenuShareAppMessage(dataTwo)
      wx.onMenuShareQQ(dataTwo)
      wx.onMenuShareWeibo(dataTwo)
      wx.onMenuShareQZone(dataTwo)
    })
  }
}
</script>
<style lang="scss" scoped>
@import '../assets/css/transform.scss';
@import '../assets/css/public.scss';
.fade-enter-active{
  transition: opacity 3s
}
.fade-enter{
  opacity: 0
}
.one {
  position: relative;
  // background: url(../../static/img/page1.png);
  /* 背景图垂直、水平均居中 */
  background-position: center center;
  /* 背景图不平铺 */
  background-repeat: no-repeat;
  /* 当内容高度大于图片高度时，背景图像的位置相对于viewport固定 */
  background-attachment: fixed;
  /* 让背景图基于容器大小伸缩 */
  background-size: cover;
  /* 设置背景颜色，背景图加载过程中会显示背景色 */
  background-color: #464646;
}
.content{
  padding-top: 11.5rem;
  position: absolute;
}
.logo {
  width: 29%;
  position: fixed;
  bottom: 0.1rem;
  margin-left: 2.2rem;
}
p{
  box-sizing: border-box;
  padding: 10px;
  background: #EAF2F9;
  opacity: 0.75;
  border-radius: 0.2rem;
  width: 75%;
  margin: 0 auto;
  margin-top: 0.3rem;
  img {
  width: 0.3rem;
  height: auto;
  display: none;
  }
}
.Ticking {
  width: 6%;
}
.title {
  padding-top: 0.6rem;
  border-radius: 0.3rem;
  line-height: 0.35rem;
}
p:nth-child(6) {
  font-size: 0.4rem;
  background: none;
  text-align: center;
  font-weight: bolder;
}
</style>



