<template>
  <transition name='fade'> 
  <div class="one" :style="homeHight">
    <div class="content" :class='{Begin: isBegin, End: isEnd}'>
      <p class="title">5、{{ title }}</p>
      <p v-for='(item,index) in optionsData' @click='showItem(index)'>
        <!-- <img :src="item.imgdata" :class='{showItemCss: item.isshowItem}'> --> {{item.text}}
      </p>
      <p> 5 / 8 </p>
    </div>
    <img @load='showContent' class="logo" src="../../static/img/bit_logo.png">
  </div>
  </transition>
</template>
<script>
export default {
  data () {
    return {
      title: '',
      optionsData: [{
        text: ' '
      }, {
        text: ' '
      }, {
        text: ' '
      }, {
        text: ''
      }],
      answer: '',
      isBegin: false,
      isEnd: false,
      homeHight: {
        height: '',
        background: ''
      }
    }
  },
  methods: {
    showItem (index) {
      const path = '/6/'
      setTimeout(() => this.$router.push({path: path}), 1000)
      this.isEnd = true
      if (index === this.answer) {
        let answerData = this.$store.getters.flagData[3].answer
        this.$store.commit('FIVE', {
          data: true,
          answer: '5、' + answerData
        })
      }
    },
    showContent () {
      this.isBegin = true
    }
  },
  created () {
    this.homeHight.height = document.documentElement.clientHeight + 'px'
    let data = this.$store.getters.flagData[3]
    this.homeHight.backgroundImage = data.bgImg
    // console.warn(this.homeHight.background)
    this.title = data.title
    this.optionsData = data.option
    // 查看答案
    for (let i = 0; i < 4; i++) {
      if (data.option[i].answerRight) {
        this.answer = i
        // console.log(this.answer)
      }
    }
    let answerData = this.$store.getters.flagData[3].answer
    this.$store.commit('FIVE', {
      data: false,
      answer: '5、' + answerData
    })
  }
}
</script>
<style lang="scss" scoped>
@import '../assets/css/transform.scss';
@import '../assets/css/public.scss';
.one {
  position: relative;
  // background: url(../../static/img/page1.png);
  /* 背景图垂直、水平均居中 */
  background-position: center center;
  /* 背景图不平铺 */
  background-repeat: no-repeat;
  /* 当内容高度大于图片高度时，背景图像的位置相对于viewport固定 */
  background-attachment: fixed;
  /* 让背景图基于容器大小伸缩 */
  background-size: cover;
  /* 设置背景颜色，背景图加载过程中会显示背景色 */
  background-color: #464646;
}
.content{
  padding-top: 11.5rem;
  position: absolute;
}
.logo {
  width: 29%;
  position: fixed;
  bottom: 0.1rem;
  margin-left: 2.2rem;
}
p{
  box-sizing: border-box;
  padding: 10px;
  background: #EAF2F9;
  opacity: 0.75;
  border-radius: 0.2rem;
  width: 75%;
  margin: 0 auto;
  margin-top: 0.3rem;
  img {
  width: 0.3rem;
  height: auto;
  display: none;
  }
}
.Ticking {
  width: 6%;
}
.title {
  padding-top: 0.6rem;
  border-radius: 0.3rem;
  line-height: 0.35rem;
}
p:nth-child(6) {
  font-size: 0.4rem;
  background: none;
  text-align: center;
  font-weight: bolder;
}
</style>



