<template>
  <div class="one" :style="homeHight">
    <div class="content" :class='{Begin: isBegin, End: isEnd}'>
      <p class="title" :class='{touming: isShowImg}'>6、回忆童年，五毛钱的零食是最美味的，最便宜的的玩具也是最好玩儿的~不管是翻花绳、玻璃弹珠、弹力球、悠悠球……呲溜，说着说着心就痒了~图中的小玩意是什么，你还记得吗？<br/>
      <img src="https://ooo.0o0.ooo/2017/06/01/592fc32290952.png"
      class='touming'
      @click='showimg'
      :class='{out_Img: isHideImg, in_Img: isShowImg,}'>
      </p>
      <p :class='{touming: isShowImg}' 
      v-for='(item,index) in optionsData'  
      @click='showItem(index)' >
      {{item.text}}
      </p>
      <p> 6 / 8 </p>
    </div>
    <img @load='showContent' class="logo" src="../../static/img/bit_logo.png">
  </div>
</template>
<script>
export default {
  data () {
    return {
      optionsData: [{
        text: ' A、拉蜘蛛丝的'
      }, {
        text: 'B、类似502的胶水'
      }, {
        text: ' C、一次性牙膏'
      }, {
        text: ' D、吹泡泡的'
      }],
      isBegin: false,
      isEnd: false,
      homeHight: {
        height: ''
      },
      isHideImg: false,
      isShowImg: false
    }
  },
  methods: {
    showItem (index) {
      this.isHideImg = true
      this.isShowImg = false
      const path = '/7/'
      setTimeout(() => this.$router.push({path: path}), 1000)
      this.isEnd = true
      if (index === 3) {
        this.$store.commit('SIX', {
          data: true,
          answer: '6、图中的小玩意儿是用来吹泡泡的。'
        })
      }
    },
    showContent () {
      this.isBegin = true
    },
    showimg (index) {
      if (this.isShowImg) {
        this.isShowImg = false
      } else {
        this.isShowImg = true
      }
    }
  },
  created () {
    this.$store.commit('SIX', {
      data: false,
      answer: '6、图中的小玩意儿是用来吹泡泡的。'
    })
    this.homeHight.height = document.documentElement.clientHeight + 'px'
  }
}
</script>
<style lang="scss" scoped>
@import '../assets/css/transform.scss';
@import '../assets/css/public.scss';
@import '../assets/css/Image_zoom.scss';
.one {
  position: relative;
  background-image: url(https://ooo.0o0.ooo/2017/06/01/592fbe95296c2.png);
  /* 背景图垂直、水平均居中 */
  background-position: center center;
  /* 背景图不平铺 */
  background-repeat: no-repeat;
  /* 当内容高度大于图片高度时，背景图像的位置相对于viewport固定 */
  background-attachment: fixed;
  /* 让背景图基于容器大小伸缩 */
  background-size: cover;
  /* 设置背景颜色，背景图加载过程中会显示背景色 */
  background-color: #464646;
}
.content{
  padding-top: 11rem;
  position: absolute;
}
.logo {
  width: 29%;
  position: fixed;
  bottom: 0.1rem;
  margin-left: 2.2rem;
}
p{
  box-sizing: border-box;
  padding: 10px;
  background: #E9F2F9;
  opacity: 0.75;
  border-radius: 0.2rem;
  width: 75%;
  margin: 0 auto;
  margin-top: 0.3rem;
  img {
  width: 0.3rem;
  height: auto;
  display: none;
  }
}
.touming{
  opacity: 1;
}
.Ticking {
  width: 6%;
}
.title {
  padding:0.3rem 0.5rem;
  border-radius: 1rem;
  line-height: 0.4rem;
  width:90%;
  height: 3.6rem;
  img{
    width: 1.15rem;
    margin-left: 1.6rem;
    display: inline-block;
  }
}
p:nth-child(6) {
  font-size: 0.4rem;
  background: none;
  text-align: center;
  font-weight: bolder;
  margin-top: 0.1rem;
}
</style>



