<template>
  <div class="home" :class="{donghua: isdonghua}" :style="homeHight">
    <div class="content">
      <img class="title" src="../../static/img/title.png">
      <div @click='Answer'>
        <img class="btn_bg" src="../../static/img/bg_button.png">
        <img class="btn_text" src="../../static/img/bt_text.png">
      </div>
      <div class="text_daoyu">
        <p>当拿着辣条的皮卡丘、读着格林童话的奥特曼<p>
        <p>跳着皮筋的暴龙兽在六一这天齐聚一堂<p>
        <p>快让我们通过时光隧道来回忆那些有他们陪伴的日子吧</p>
      </div>
      <img class="logo" src="../../static/img/bit_logo.png">
    </div>
  </div>
</template>
<script>
import { sampleSize } from 'lodash'
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      isdonghua: false,
      homeHight: {
        height: ''
      }
    }
  },
  methods: {
    Answer () {
      const path = '/1/'
      setTimeout(() => this.$router.push({path: path}), 700)
      this.isdonghua = true
      var _this = this
      var temp = []
      let arrTempLength = this.$store.state.data.titleData.length
      for (var i = 0; i < arrTempLength; i++) {
        temp[i] = i + 1
      }
      let dateArr = []
      dateArr = sampleSize(temp, 6)
      for (let i = 0; i < dateArr.length; i++) {
        _this.$store.dispatch('initdata', dateArr[i] - 1)
        // console.log(i)
      }(i)
    }
  },
  created () {
    this.homeHight.height = document.documentElement.clientHeight + 'px'
  }
}
</script>
<style lang="scss" scoped>
.home {
  background-image: url(https://ooo.0o0.ooo/2017/06/01/592fbe953caf6.png);
  /* 背景图垂直、水平均居中 */
  background-position: center center;
  /* 背景图不平铺 */
  background-repeat: no-repeat;
  /* 当内容高度大于图片高度时，背景图像的位置相对于viewport固定 */
  background-attachment: fixed;
  /* 让背景图基于容器大小伸缩 */
  background-size: cover;
  /* 设置背景颜色，背景图加载过程中会显示背景色 */
  background-color: #464646;
}
img{
  width:20%;
}

.donghua {
  -webkit-transform: scale(1.3);
  -moz-transform: scale(1.3);
  -webkit-transition-duration: 3s;
  -moz-transition-duration: 3s;
}

.home {
  display: flex;
  position: relative;
  img,.text_daoyu {
    position: absolute;
  }
  .bg {
    width: 100%;
    height: 11.38rem;
  }
  .title,
  .btn_bg,
  .btn_text,
  .logo {
    width: 20%;
    height: auto;
    top: -6.5rem;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
  .title {
    left: 23px;
  }
  .btn_bg,
  .btn_text {
    width: 28%;
    top: 2.5rem;
    left: 0.3rem;
  }
  .btn_text {
    width: 22%;
  }
  .text_daoyu {
    width: 92%;
    top: 7.6rem;
    margin-left: 0.25rem;
    text-align: center;
    line-height: 0.4rem;
    p{
      font-size: 0.2rem;
    }
  }
  .logo {
    width: 29%;
    position: fixed;
    top: 9rem;
  }
}
</style>



