import Vue from 'vue'
import Vuex from 'vuex'
import home from './home.js'
import data from './data.js'

Vue.use(Vuex)

const DEBUG = process.env.NODE_ENV !== 'production'
export default new Vuex.Store({
  modules: {
    home,
    data
  },
  strict: DEBUG
})



