<template>
  <div class="one" :style="homeHight">
    <div class="content" 
    :class='{Begin: isBegin, End: isEnd}'>
      <p class="title" 
      :class='{touming: istouming}'>2. 小时候拥有的零花钱不多，可学校里的零食也超级便宜。课间攥着一张五毛钱，就可以去小卖部大快朵颐啦~上图所示零食的名称按顺序依次是什么呢？
        <br/>
        <img 
        v-for='(item,index) in imgDate' 
        :src="item.img" 
        @click='showimg(index)'
        :class='{out_Img: item.isHideImg, in_Img: item.isShowImg,}'
        >
      </p>
      <p :class='{touming: istouming}' 
      v-for='(item,index) in optionsData' 
      @click='showItem(index)'> 
      {{item.text}}
      </p>
      <p> 2 / 8 </p>
    </div>
    <img @load='showContent' class="logo" src="../../static/img/bit_logo.png">
  </div>
</template>

<script>
export default {
  data () {
    return {
      optionsData: [{
        text: ' A、魔法士、小当家、CC乐、拖肥'
      }, {
        text: ' B、小当家、魔法士、拖肥、CC乐'
      }, {
        text: ' C、魔法士、小当家、拖肥、CC乐'
      }, {
        text: ' D、小当家、魔法士、CC乐、拖肥'
      }],
      imgDate: [{
        img: 'https://ooo.0o0.ooo/2017/06/01/592fc15dcbe7a.png',
        isShowImg: false,
        isHideImg: false
      }, {
        img: 'https://ooo.0o0.ooo/2017/06/01/592fc15db6784.png',
        isShowImg: false,
        isHideImg: false
      }, {
        img: 'https://ooo.0o0.ooo/2017/06/01/592fc15dc4968.png',
        isShowImg: false,
        isHideImg: false
      }, {
        img: 'https://ooo.0o0.ooo/2017/06/01/592fc15dc82c3.png',
        isShowImg: false,
        isHideImg: false
      }],
      isBegin: false,
      isEnd: false,
      homeHight: {
        height: ''
      },
      istouming: false
    }
  },
  methods: {
    showItem (index) {
      this.istouming = false
      this.optionsData[index].isshowItem = true
      for (let i = 0; i < 4; i++) {
        this.imgDate[i].isHideImg = true
      }
      const path = '/3/'
      setTimeout(() => this.$router.push({path: path}), 1000)
      this.isEnd = true
      if (index === 1) {
        this.$store.commit('TWO', {
          data: true,
          answer: '2、零食的名称按顺序依次是小当家 魔法士 拖肥 CC乐。'
        })
      }
    },
    showContent () {
      this.isBegin = true
    },
    showimg (index) {
      console.log(index)
      for (let i = 0; i < 4; i++) {
        this.imgDate[i].isHideImg = false
        if (i !== index) {
          this.imgDate[i].isShowImg = false
          this.istouming = false
        }
      }
      if (this.imgDate[index].isShowImg) {
        this.imgDate[index].isHideImg = true
      } else {
        this.imgDate[index].isShowImg = true
        this.istouming = true
      }
    }
  },
  created () {
    this.homeHight.height = document.documentElement.clientHeight + 'px'
    this.$store.commit('TWO', {
      data: false,
      answer: '2、零食的名称按顺序依次是小当家 魔法士 拖肥 CC乐。'
    })
  }
}
</script>
<style lang="scss" scoped>
@import '../assets/css/transform.scss';
@import '../assets/css/public.scss';
@import '../assets/css/Image_zoom.scss';
.one {
  position: relative;
  background-image: url(https://ooo.0o0.ooo/2017/06/01/592fbe95056f5.png);
  /* 背景图垂直、水平均居中 */
  background-position: center center;
  /* 背景图不平铺 */
  background-repeat: no-repeat;
  /* 当内容高度大于图片高度时，背景图像的位置相对于viewport固定 */
  background-attachment: fixed;
  /* 让背景图基于容器大小伸缩 */
  background-size: cover;
  /* 设置背景颜色，背景图加载过程中会显示背景色 */
  background-color: #464646;
}
.content{
  padding-top: 11rem;
  position: absolute;
}
.logo {
  width: 29%;
  position: fixed;
  bottom: 0.1rem;
  margin-left: 2.2rem;
}
p{
  box-sizing: border-box;
  padding: 10px;
  background: #fff;
  opacity: 0.75;
  border-radius: 0.2rem;
  width: 75%;
  margin: 0 auto;
  margin-top: 0.3rem;
  img {
  width: 0.3rem;
  height: auto;
  display: none;
  }
}
.touming{
  opacity: 1;
}
.Ticking {
  width: 6%;
}
.showItemCss {
  position: absolute;
  display: inline-block;
}
.title {
  padding:0.5rem 0.5rem;
  border-radius: 1rem;
  line-height: 0.4rem;
  width:90%;
  height: 3.7rem;
  img{
    margin-left: 0.05rem;
    display:inline-block;
    width:1rem;
  }
}
p:nth-child(6) {
  font-size: 0.4rem;
  background: none;
  text-align: center;
  font-weight: bolder;
}
</style>



