<template>
  <div class="one" :style="homeHight">
    <div class="content" :class='{Begin: isBegin}'>
      <div class="title">
        <p>你一共答对了 <span class="impor">{{ count }}</span>题</p>
        <p>经计算</p>
        <p >你童心指数为<span class="impor">{{ childlike }}%</span></p>
        <p> {{ answerContent }} </p>
        <p>愿你永远天真无邪</p>
        <p>六一快乐！</p>
      </div>
      <div v-if='showAnswer' class="btn" @click='answer'>
        <img src="../../static/img/bg_button.png">
        <p>查看答案</p>
      </div>
      <div v-else class="showanswer">
        <div v-for='item in answerDate'>
          <p>{{ item.answerData }}
            <span v-if='item.isRight'>（√）</span>
            <span v-if='!item.isRight'>（×）</span>
          </p>
        </div>
      </div>
    </div>
    <img @load='showContent' class="logo" src="../../static/img/bit_logo.png">
  </div>
</template>


<script>
export default {
  data () {
    return {
      isBegin: false,
      homeHight: {
        height: ''
      },
      question: 2,
      childlike: '',
      showAnswer: true,
      answerDate: {
        one: {
          isRight: '',
          answerData: ''
        },
        two: {
          isRight: '',
          answerData: ''
        },
        three: {
          isRight: '',
          answerData: ''
        },
        four: {
          isRight: '',
          answerData: ''
        },
        five: {
          isRight: '',
          answerData: ''
        },
        six: {
          isRight: '',
          answerData: ''
        },
        seven: {
          isRight: '',
          answerData: ''
        },
        eight: {
          isRight: '',
          answerData: ''
        }
      },
      fenxiang: '',
      Api: '',
      timestamp_data: '',
      count: '',
      answerContent: ''
    }
  },
  mounted () {
    let _this = this
    // console.log(this.$store.state.home)
    this.answerDate = this.$store.state.home
    // console.warn(this.answerDate)
    // console.log(this.answerDate.one.isRight)
    if (this.answerDate.one.isRight) {
      this.count ++
    }
    if (this.answerDate.two.isRight) {
      this.count ++
    }
    if (this.answerDate.three.isRight) {
      this.count ++
    }
    if (this.answerDate.four.isRight) {
      this.count ++
    }
    if (this.answerDate.five.isRight) {
      this.count ++
    }
    if (this.answerDate.six.isRight) {
      this.count ++
    }
    if (this.answerDate.seven.isRight) {
      this.count ++
    }
    if (this.answerDate.eight.isRight) {
      this.count ++
    }
    let i = this.count
    switch (i) {
      case 0: this.childlike = 0
        break
      case 1: this.childlike = 15
        break
      case 2: this.childlike = 25
        break
      case 3: this.childlike = 40
        break
      case 4: this.childlike = 60
        break
      case 5: this.childlike = 70
        break
      case 6: this.childlike = 80
        break
      case 7: this.childlike = 99
        break
      case 8: this.childlike = 666
        break
    }
    if (i <= 3) {
      this.fenxiang = '我的童心指数是' + _this.childlike + '%,快来测测你还能不能过六一儿童节吧！'
      this.answerContent = '糟糕，我已经没法过儿童节了'
    }
    if (i > 3 && i <= 7) {
      this.fenxiang = '我的童心指数是' + _this.childlike + '%,快来测测你还能不能过六一儿童节吧！'
      this.answerContent = '人家还年轻着呢！'
    }
    if (i >= 7) {
      this.fenxiang = '我的童心指数是' + _this.childlike + '%,快来测测你还能不能过六一儿童节吧！'
      this.answerContent = '我还是个孩子，来跟我一起穿越回童年过家家吧！'
    }
    let wx = require('weixin-js-sdk')
    this.homeHight.height = document.documentElement.clientHeight + 'px'
    let title = this.fenxiang
    // console.log(this.fenxiang)
    let desc = this.answerContent
    wx.ready(function () {
      let dataOne = {
        title: title,
        link: 'https://test.stuzone.com',
        imgUrl: 'http://i4.buimg.com/524586/608bd17fa3087f9b.png'
      }
      let dataTwo = {
        title: title,
        desc: '♪(๑ᴖ◡ᴖ๑)♪儿童节快乐', // 分享描述
        link: 'https://test.stuzone.com',
        imgUrl: 'http://i4.buimg.com/524586/608bd17fa3087f9b.png'
      }
      wx.onMenuShareTimeline(dataOne)
      wx.onMenuShareAppMessage(dataTwo)
      wx.onMenuShareQQ(dataTwo)
      wx.onMenuShareWeibo(dataTwo)
      wx.onMenuShareQZone(dataTwo)
    })
  },
  methods: {
    showContent () {
      this.isBegin = true
    },
    answer () {
      this.showAnswer = false
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../assets/css/transform.scss';
@import '../assets/css/public.scss';
.impor{
      font-size: 0.5rem;
}
.showanswer{
  background: #EAF2F9;
  opacity: 0.75;
  border-radius: 0.2rem;
  width:5rem;
  margin: 0 auto;
  margin-top: 0.3rem;
  box-sizing: border-box;
  padding: 10px;
  margin-left: 0.68rem;
  p,span{
    text-align: left;
  }
  span{
    font-size: 10px;
    color: red;
  }
}
p{
font-size: 0.2rem;
font-family: STSong, FangSong, serif;
text-align: center;
line-height: 0.35rem;
}
.one {
  position: relative;
  background-image: url(https://ooo.0o0.ooo/2017/06/01/592fc2dfdea20.png);
  /* 背景图垂直、水平均居中 */
  background-position: center center;
  /* 背景图不平铺 */
  background-repeat: no-repeat;
  /* 当内容高度大于图片高度时，背景图像的位置相对于viewport固定 */
  background-attachment: fixed;
  /* 让背景图基于容器大小伸缩 */
  background-size: cover;
  /* 设置背景颜色，背景图加载过程中会显示背景色 */
  background-color: #464646;
}
.content{
  padding-top: 11.5rem;
  position: absolute;
}
.logo {
  width: 29%;
  position: fixed;
  bottom: 0.1rem;
  margin-left: 2.2rem;
}
.showItemCss {
  position: absolute;
  display: inline-block;
}
.title{
  background: #EAF2F9;
  opacity: 0.75;
  border-radius: 0.2rem;
  width:5rem;
  margin: 0 auto;
  margin-top: 0.15rem;
  box-sizing: border-box;
  padding: 10px;
  margin-left: 0.68rem;
}
.btn{
  position: relative;
  margin-top: 1rem;
  margin-left: 2rem;
  img,p{
    position:absolute;
  }
  img{
    width:2.5rem;
  }
  p{
    font-size: 0.4rem;
    top:1rem;
    left:0.38rem;
  }
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
</style>



