<template>
  <div id="app">
    <div @click='btn'>
      <img class="Music" :class='{rotate: isrotate}' src="../static/img/music.png">
      <audio id="bgMusic" src="../static/music.mp3" autoplay="preload"></audio>
    </div>
  <router-view></router-view>
  </div>
</template>

<script>
import Qs from 'qs'
import axios from 'axios'
export default {
  name: 'app',
  data () {
    return {
      isrotate: true,
      Api: ''
    }
  },
  methods: {
    btn () {
      let music = document.getElementById('bgMusic')
      if (this.isrotate) {
        this.isrotate = false
        music.pause()
      } else {
        this.isrotate = true
        music.play()
      }
    }
  },
  mounted () {
    var _this = this
    let urlPage = location.href.split('#')[0]
    let urlData = {
      url: urlPage
    }
    let url = Qs.stringify(urlData)
    axios({
      method: 'post',
      url: 'https://test.stuzone.com/api',
      data: url,
      headers: {'Content-Type': 'application/x-www-form-urlencoded'}
    })
    .then((response) => {
      this.Api = response.data.data
      // console.log(this.Api)
      this.$store.dispatch('wxapi', {
        timestamp: _this.Api.timestamp,
        nonceStr: _this.Api.nonceStr,
        signature: _this.Api.signature
      })
    })
    .catch((error) => {
      console.log(error)
    })
  },
  destroyed () {
    // console.log('456')
  }
}
</script>
<style>
.Music {
  background-image: url();
  font-size: 16px;
  position: fixed;
  z-index: 2;
  top: 0.5rem;
  left: 5.4rem;
  width: 0.5rem;
}
.rotate {
  -webkit-animation: rotating 1.2s linear infinite;
}
@-webkit-keyframes rotating {
  from {
    -webkit-transform: rotate(0)
  }
  to {
    -webkit-transform: rotate(360deg)
  }
}
@keyframes rotating {
  from {
    transform: rotate(0)
  }
  to {
    transform: rotate(360deg)
  }
}
@-moz-keyframes rotating {
  from {
    -moz-transform: rotate(0)
  }
  to {
    -moz-transform: rotate(360deg)
  }
}
</style>




